@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap);
* {
  box-sizing: border-box;
}

:root {
  --primary: #6948ff;
  --secondary: #553bcb;
  --lightgrey: #d3d3d3;
  --grey: #808080;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  font-family: 'Montserrat', sans-serif; /* Always be sure to add a secondary font because some browsers may not support the font you choose */
  line-height: 1.5;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.primary-color {
  color: #6948ff;
  color: var(--primary);
}

button {
  width: 100%;
  padding: 8px 0;
  border-radius: 4px;
  border: 1px solid #6948ff;
  border: 1px solid var(--primary);
  background-color: #6948ff;
  background-color: var(--primary);
  color: #fff;
  cursor: pointer;
}

button:hover {
  background-color: #553bcb;
  background-color: var(--secondary);
  border: 1px solid #553bcb;
  border: 1px solid var(--secondary);
}
.header {
    width: 100%;
    height: 100px;
    background-color: #22262a;
  }
  
  .header .navbar {
    max-width: 1240px;
    height: 100%;
    padding: 0 1rem;
    margin: auto;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
  }

  .header .logo {
      display: flex;
      align-items: center;
  }
  
  .header .navbar img {
    width: 80px;
    height: auto;
  }
  
  .header .nav-menu {
    display: flex;
    align-items: center;
    justify-self: start;
  }

  .header .nav-item {
    padding: 0 1.5rem;
  }
  
  .header .hamburger {
    position: absolute;
    top: 2rem;
    right: 1rem;
    display: none;
  }
  
  /* .header .bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    background-color: var(--lightgrey);
  } */



  @media screen and (max-width: 960px) {

    /* Navbar */
    .header .nav-menu {
      position: fixed;
      left: -100%;
      flex-direction: column;
      justify-content: center;
      background-color: #22262a;
      width: 100%;
      height: 100%;
      text-align: center;
      transition: 0.3s;
    }
    
    .header .nav-menu.active {
      left: 0;
    }
    
    .header .nav-item {
      margin: 1.5rem 0;
    }
    
    .header .hamburger {
      display: block;
      cursor: pointer;
    }
    
    /* Hamburger animation */
    .hamburger.active .bar:nth-child(2) {
      opacity: 0;
    }
    
    .hamburger.active .bar:nth-child(1) {
      -webkit-transform: translateY(8px) rotate(45deg);
              transform: translateY(8px) rotate(45deg);
    }
    
    .hamburger.active .bar:nth-child(3) {
      -webkit-transform: translateY(-8px) rotate(-45deg);
              transform: translateY(-8px) rotate(-45deg);
    }
    
}
.hero {
    width: 100%;
    height: 100vh;
    background-color: #22262a;
  }
  
  .hero .container {
    max-width: 1240px;
    padding: 0 1rem;
    margin: auto;
    overflow: hidden;
  }
  
  .hero .navbar {
    width: 100%;
    height: 100px;
    padding: 4px 0;
    display: flex;
  }
  
  .hero .navbar img {
    width: 80px;
    height: auto;
    margin-right: 1rem;
  }
  
  .hero .navbar .nav {
    display: flex;
    align-items: center;
    justify-self: start;
  }
  
  /* .hero .navbar i {
   color: var(--lightgrey);
    display: none;
    position: absolute;
    right: 1rem;
    top: 1rem;
  } */
  
  /* .hero .navbar .login {
    display: flex;
    align-items: center;
    justify-content: center;
    justify-self: end;
  } */
  
  .hero .navbar li {
    padding: 0 1.6rem;
  }
  
  .hero .navbar .btn {
    background-color: #525b65;
    padding: 10px 22px;
    border-radius: 8px;
  }
  
  /* Hero Content */
  .hero .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  
  .hero .col-1 {
    padding: 1rem 1rem 1rem 0;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  
  .hero h1 {
    font-size: 42px;
    font-weight: 700;
    color: #fff;
  }
  
  .hero .col-1 p {
    font-size: 18px;
    color: var(--grey);
    line-height: 1.4;
    margin-top: 1.2rem;
  }
  
  .hero .used-by p {
    color: var(--lightgrey);
    font-size: 12px;
    margin: 2rem 0 1rem 0;
  }
  
  .hero .col-1 .icons i {
    color: var(--grey);
    padding-right: 1rem;
  }
  
  /* Hero Right-Side */
  
  .hero .col-2 {
    padding: 1rem;
    height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 0.8rem;
  }
  
  .hero .col-2 p {
    color: var(--grey);
  }
  
  .hero .form-layout {
    width: 400px;
    margin: auto;
    background-color: #fff;
    border-radius: 6px;
  }
  .hero .form-container {
    padding: 1.5rem 2rem;
  }

  .hero .sign-in-txt {
    margin-bottom: 10px;
  }
  
  .hero .social-login i {
    background: #fff;
    padding: 16px 45px 6px 45px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
  }

  .hero .col-2 .divider p {
    width: 100%;
    text-align: center;
    border-bottom: 1px solid var(--lightgrey);
    line-height: 0.1em;
    margin: 2rem 0 2rem;
  }
  
  .hero .col-2 .divider p span {
    background: #fff;
    padding: 0 10px;
  }
  
  .hero form input {
    display: block;
    width: 100%;
    margin: 1.2rem 0;
    padding: 8px;
    border: 1px solid var(--lightgrey);
    border-radius: 4px;
  }
  
  
  .hero .form-footer {
    border-top: 1px solid var(--lightgrey);
    background-color: #f8f8f8;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
  }
  
  .hero .form-footer p {
    padding: 1.5rem 2rem;
  }
  
  .hero .form-footer span {
    font-weight: 500;
  }



  @media screen and (max-width: 960px) {

    .hero {
      height: auto;
      width: 100%;
    }
  
    .hero .container {
      padding: 0 1rem;
    }
  
    .hero .content {
      grid-template-columns: 1fr;
    }
  
    .hero .col-1 {
      height: 90vh;
      padding: .2rem;
      /* margin-top: 3rem; */
      max-width: 100%;
    }
  
    .hero h1 {
      font-size: 32px;
    }
  
    .hero .col-2 {
      display: none;
    }
}

/* Section B (Pricing) */

.section-b {
  width: 100%;
  margin-top: 5rem;
}

.section-b .container {
  max-width: 1240px;
  padding: 1rem;
  margin: auto;
  overflow: hidden;
}

.section-b .top-content {
  display: grid;
  grid-template-columns: 3fr 1fr;
  border-bottom: 1px solid var(--lightgrey);
  padding-bottom: 3rem;
}

.section-b .top-content h2 {
  font-size: 38px;
  padding-bottom: 0.5rem;
}

.section-b .top-content span {
  color: var(--primary);
}

.section-b p {
  color: var(--grey);
}



.section-b .bottom-content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin: 3rem auto;
}

.section-b .card {
  display: flex;
  flex-direction: column;
  padding: 1rem 1rem 1rem 0;
}

.section-b i {
  color: green;
  padding-right: 6px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.section-b .card div {
  display: flex;
  border-bottom: 1px solid var(--lightgrey);
  padding: 1rem 0;
}

.section-b .card .no-border {
  border: none;
}

.section-b .card span {
  color: var(--primary);
  text-transform: uppercase;
  font-weight: 500;
  font-size: 14px;
}

.section-b .card h3 {
  font-size: 28px;
  margin: 0.7rem 0;
}



@media screen and (max-width: 960px) {

  /* Section B */
  .section-b .top-content {
    grid-template-columns: 1fr;
  }
.section-b .container .bottom-content {
  display: none;
}

.section-b .top-content h2 {
  font-size: 32px;
}

}
/* Section-C (Testimonials) */
.section-c {
    width: 100%;
    color: #fff;
    margin: 3rem 0;
  }
  
  .section-c .container {
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
  }
  
  .section-c .testimonials {
    background-image: linear-gradient(
        to left,
        rgba(73, 50, 173, 0.5),
        rgba(73, 50, 173, 0.9)
      ),
      url('https://images.unsplash.com/photo-1562071707-7249ab429b2a?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1650&q=80');
    width: 100%;
    height: 450px;
    background-position: left;
    border-radius: 10px;
  }
  
  .section-c .content {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 50%;
    justify-content: center;
    padding: 3rem;
  }
  
  .section-c .body {
    padding: 1.2rem 0;
  }
  
  .section-c .name p {
    font-size: 14px;
  }
  


  
  @media screen and (max-width: 960px) {
  

  /* Section C */
  .section-c {
    margin: 1rem 0;
  }
  .section-c .content {
    max-width: 100%;
  }
  
}
/* Section-D (FAQ) */
.section-d {
    width: 100%;
    margin: 3rem 0;
  }
  
  .section-d .container {
    max-width: 1240px;
    padding: 1rem;
    margin: auto;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  
  .section-d h3 {
    font-size: 28px;
    padding: 1rem 0;
  }
  
  .section-d .col-1 p {
    color: var(--grey);
  }
  
  .section-d span {
    color: var(--primary);
  }
  
  .section-d h5 {
    font-size: 1rem;
    padding: 0.4rem 0;
  }
  
  .section-d .faq {
    padding: 1.2rem 0;
  }
  
  .section-d .faq p {
    color: var(--grey);
  }



@media screen and (max-width: 960px) {

    /* Section D */
    .section-d {
      margin: 1rem 0;
    }
    .section-d .container {
    grid-template-columns: 1fr;
  }

  }
/* Section-E (Contact) */
.section-e {
    width: 100%;
    margin: 3rem 0;
    display: flex;
  }
  
  .section-e .container {
    max-width: 1240px;
    margin: auto;
    overflow: hidden;
    display: flex;
  }
  
  .section-e .col-1 {
    flex: 1 0 45%;
    padding: 5rem 2rem;
  }
  
  .section-e .col-1 h2 {
    font-size: 28px;
    padding: 0.4rem 0;
  }
  .section-e p {
    color: var(--grey);
  }
  
  .section-e .col-1 .address {
    padding: 1rem 0;
  }
  
  .section-e .col-1 .icons {
    display: flex;
    align-items: center;
    padding: 0.4rem 0;
  }
  

  
  .section-e .careers {
    padding: 2rem 0;
  }
  
  .section-e span {
    color: var(--primary);
  }
  
  .section-e .col-2 {
    flex: 1 0 55%;
    padding: 5rem 2rem;
  }
  
  .section-e form {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin: auto;
  }
  
  .section-e input {
    margin-bottom: 1rem;
    padding: 0.6rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid var(--lightgrey);
  }
  .section-e textarea {
    margin-bottom: 1rem;
    padding: 0.6rem;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid var(--lightgrey);
    font-family: 'Montserrat', sans-serif;
  }
  
  .section-e form .checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
  }
  
  .section-e form .checkbox {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1rem 0;
  }
  
  .section-e .checkbox input {
    margin: 0;
    padding-right: 4px;
  }
  
  .section-e .checkbox p {
    font-size: 12px;
    padding-left: 4px;
  }
 
  
  
  @media screen and (max-width: 960px) {
  
  /* Section E */
  .section-e {
    margin: 1 0;
  }
  .section-e .container {
    flex-direction: column;
  }
  
  .section-e .col-1 {
    padding: 0 1rem;
  }
  
  .section-e .col-2 {
    padding: 1rem;
  }
  }  

.footer {
  width: 100%;
  background-color: #22262a;
}

.footer .container {
  max-width: 1240px;
  padding: 3rem 1rem;
  margin: auto;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
}

.footer h6,
p {
  color: var(--lightgrey);
  font-size: 14px;
  padding: 0.4rem 0;
}

.footer a {
  color: var(--lightgrey);
  font-size: 14px;
  padding: 0.4rem 0;
  display: block;
}

.footer .subscribe {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer input {
  padding: 0.4rem;
  font-size: 14px;
  border-radius: 4px;
  width: 250px;
  border: 1px solid var(--lightgrey);
}

.footer button {
  margin-left: 0.8rem;
  width: 150px;
}



.footer-bottom .content {
  max-width: 1240px;
  margin: auto;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid var(--grey);
}

.footer-bottom i {
  color: var(--lightgrey);
  padding: 0 0.4rem;
}


@media screen and (max-width: 960px) {
  
  /* Footer */
  .footer .container {
    /* grid-template-columns: 1fr 1fr 1fr;
    grid-row: auto; */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1rem;
  }
  
  .footer .solutions, .support {
    display: none;
  }
  
  .footer .col-subscribe {
    margin: 2rem;
    text-align: center;
  }
  
  .footer .subscribe {
    flex-direction: column;
  }
  
  .footer input {
    width: 100%;
  }
  
  .footer button {
    width: 100%;
    margin: .2rem 0;
  }
  
  .footer-bottom .content {
    padding: 1rem;
    justify-content: center;
  }
  
  .footer-bottom .rights {
    display: none;
  }

  
  }
